

















import {EditMixin, EmitMixin, PropsMixinFactory, RefsMixinFactory, StyleMixin} from "../../utils/mixins";

export default {
    name: "pl-number",
    mixins: [
        StyleMixin,
        EditMixin,
        EmitMixin,
        PropsMixinFactory.create({
            min: PropsMixinFactory.Number,
            max: PropsMixinFactory.Number,
            step: PropsMixinFactory.Number,
            precision: PropsMixinFactory.Number,
        }),
        RefsMixinFactory({
            innerInput: Object,
            input: Object
        })
    ],
    emitters: {
        emitFocus: Function,
        emitBlur: Function,
        emitInput: Function,
    },
    props: {
        value: {type: [String, Object]},                            // 
        min: {type: [String, Number]},                              // 
        max: {type: [String, Number]},                              // 
        step: {type: [String, Number]},                             // 
        stepStrictly: {type: Boolean},                              //  step 
        precision: {type: Number},                                  // 
        hideButton: {type: Boolean},                                // 
        inputProps: {type: Object},                                 // pl-input
    },
    watch: {
        value(val) {
            this.p_value = val
        },
    },
    data() {
        return {
            isFocus: false,                                         // input
            p_value: this.value,
        }
    },
    methods: {
        onFocus(e) {
            this.isFocus = true
            this.emitFocus(e)
        },
        onBlur(e) {
            this.isFocus = false
            this.emitBlur(e)
        },
        onInput(e) {
            this.p_value = e.target.value
            this.emitInput(this.p_value)
        },
    },
}
